import React from 'react';
import CodeMirror from "@uiw/react-codemirror";
import { json } from '@codemirror/lang-json';
import { copilot } from '@uiw/codemirror-theme-copilot';
import rainbowBrackets from 'rainbowbrackets';
import { indentUnit } from '@codemirror/language';
import { lintGutter, linter } from '@codemirror/lint';

// The React component for the CodeMirror editor
export const JsonMirror = ({ content, readOnly, onChange }) => {
  // Lint function for JSON syntax validation
  const lintJson = (view) => {
    const errors = [];
    try {
      JSON.parse(view.state.doc.toString()); // Validate JSON syntax
    } catch (error) {
      errors.push({
        from: 0,
        to: view.state.doc.length,
        severity: "error",
        message: error.message,
      });
    }
    return errors;
  };

  return (
      <CodeMirror
        value={content}
        theme={copilot}
        extensions={[
          json(),
          rainbowBrackets(),
          indentUnit.of("    "),
          lintGutter(),
          linter(lintJson),
        ]}
        editable={!readOnly}
        onChange={onChange} // Pass onChange handler for content updates
      />
  );
};
