import React from 'react';
import ReactDOM from 'react-dom/client';
import { JsonMirror } from './component.jsx'; // Import the React component

class temporaryElement extends HTMLElement {
    constructor() {
        super();
        const shadow = this.attachShadow({ mode: 'closed' });

        // Create and append styles
        const style = document.createElement('style');
        style.textContent = `
            .cm-editor {
                width: fit-content !important;
                min-width: var(--temp-width) !important;
            }
            .cm-scroller {
                overflow: visible !important;
            }
            .cm-gutters {
                height: var(--temp-height) !important;
            }
            .cm-theme {
                width: fit-content !important;
                height: fit-content !important;
            }
            .cm-main {
                width: fit-content !important;
                height: fit-content !important;
            }
        `;
        
        shadow.appendChild(style);
        
        // Create the mount point for the editor
        this.mountPoint = document.createElement('div');
        this.mountPoint.classList.add('cm-main');
        shadow.appendChild(this.mountPoint);
    }

    connectedCallback() {
        this.render();
    }  

    attributeChangedCallback(name, oldValue, newValue) {
        if (oldValue !== newValue) {
            this.render();
        }
    }

    get readOnly() {
        return this.hasAttribute('readonly');
    }

    setContent(content) {
        this.innerHTML = content;
        this.render();
    }

    getContent() {
        return this.innerHTML.trim();
    }

    // Method to get the current height of the editor
    getHeight() {
        // Get the bounding rectangle of the element
        return this.mountPoint.getBoundingClientRect().height;
    }

    // Method to get the current width of the editor
    getWidth() {
        // Get the bounding rectangle of the element
        return this.mountPoint.getBoundingClientRect().width;
    }

    handleChange = (value) => {
        const event = new CustomEvent('change', {
            detail: { value },
        });
        this.dispatchEvent(event);
    };

    render() {
        const root = ReactDOM.createRoot(this.mountPoint);
        const content = this.innerHTML.trim();

        root.render(
                <JsonMirror
                    content={content}
                    readOnly={this.readOnly}
                    onChange={this.handleChange}
                />
        );
    }

    disconnectedCallback() {
        const root = ReactDOM.createRoot(this.mountPoint);
        root.unmount();
    }
}

customElements.define('temporary-element', temporaryElement);
