import './element.jsx';  // Import the custom element

import 'overlayscrollbars/styles/overlayscrollbars.css';
import { OverlayScrollbars } from 'overlayscrollbars';


const template = document.createElement('template');
template.innerHTML = `
    <style>${getCSS()}</style>
    <link type="text/css" href="https://cdnjs.cloudflare.com/ajax/libs/overlayscrollbars/2.10.0/styles/overlayscrollbars.min.css" rel="stylesheet"/>
    ${getHTML()}
`;

class CustomTextarea extends HTMLElement {
    constructor() {
        super();
        const shadow = this.attachShadow({mode:'closed'});
        shadow.appendChild(template.content.cloneNode(true));

        (async () => {
            // When we get the scrollbar from the iframe, attach it to the
            // divtag in our shadow
            try {
                const scroll = shadow.querySelector('#os-container');
                const obj = OverlayScrollbars(scroll, {}); // Initialize the scrollbar
            } catch (error) {
                console.error('Error initializing scrollbar:', error);
            }
            const editor = shadow.getElementById('editor');
            const editorPlaceholder = shadow.getElementById('editor-placeholder');
            const osPlaceholder = shadow.getElementById('os-placeholder');
            const osContainer = shadow.getElementById('os-container');

            // Adjust placeholder size on editor change
            editor.addEventListener('change', () => {
                editorPlaceholder.style.width = editor.getWidth() + 'px';
                editorPlaceholder.style.height = editor.getHeight() + 'px';
            });

            // Synchronize scroll between osPlaceholder and osContainer
            osPlaceholder.addEventListener('scroll', () => {
                osContainer.scrollTop = osPlaceholder.scrollTop;
                osContainer.scrollLeft = osPlaceholder.scrollLeft;
            });

            osContainer.addEventListener('scroll', () => {
                osPlaceholder.scrollTop = osContainer.scrollTop;
                osPlaceholder.scrollLeft = osContainer.scrollLeft;
            });

            // Handle scroll for OverlayScrollbars
            const overlayViewport = shadow.querySelector("[data-overlayscrollbars-viewport]");
            if (overlayViewport) {
                overlayViewport.addEventListener('scroll', () => {
                    osPlaceholder.scrollTop = overlayViewport.scrollTop;
                    osPlaceholder.scrollLeft = overlayViewport.scrollLeft;
                });
            }
        })();
    }
}

// Define the custom element
customElements.define('json-mirror', CustomTextarea);

function getHTML() {
    return `
        <div id="os-placeholder">
            <div id="os-container">
                <temporary-element id="editor"></temporary-element>
                <div id="editor-placeholder"></div>
                <div id="scroll-margin"></div>
            </div>
        </div>
    `;
}

// This is a placeholder for a template import
function getCSS() {
    return `
        #editor {
            --temp-width: var(--editor-width);
            --temp-height: var(--editor-height);
            width: fit-content !important;
            height: fit-content !important;
        }
        #editor-placeholder {
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
        }
        #os-container {
            width: var(--editor-width) !important;
            height: var(--editor-height) !important;
            overflow: auto !important;
        }
        .os-scrollbar-handle {
            background-color: #707070 !important;
        }
        #scroll-margin {
            height: 10px !important;
            width: 100% !important;
            background-color: #232a2f !important;
            position: sticky !important;
            bottom: 0 !important;
            left: 0 !important;
        }
        #os-placeholder {
            overflow: hidden !important;
            width: var(--editor-width) !important;
            height: var(--editor-height) !important;
        }
    `;
}